.main {
  z-index: 2;
  background-image: url("../img/main-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-color: #292929;
  padding: 17.5rem 0 11.25rem;
  &__inner {
    position: relative;
    z-index: 2;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
  h1 {
    max-width: 45rem;
    font-size: 3.5rem;
    line-height: 100%;
    color: rgb(186, 179, 145);
    margin: 0;
    .text_color {
      color: rgb(255, 255, 255);
    }
  }
  ul {
    max-width: 40.625rem;
    display: grid;
    grid-template-columns: 100%;
    gap: 1.5rem;
    margin-top: 3.5rem;
    li {
      display: flex;
    }
    li i {
      flex-shrink: 0;
      width: 1.625rem;
      margin-right: 2rem;
    }
    li i img {
      width: 100%;
    }
    li p {
      font-size: 1.125rem;
      line-height: 130%;
      font-weight: 500;
      color: rgb(255, 255, 255);
      margin: 0;
    }
    li p strong {
      font-weight: 700;
    }
  }
  &__group {
    display: flex;
    align-items: stretch;
    gap: 3.125rem;
    margin-top: 6.25rem;
  }
  &__info {
    position: relative;
    flex-shrink: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgb(255, 255, 255);
    border-radius: 0.4375rem;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      right: -0.875rem;
      transform: translate(0, -50%) rotate(90deg);
      width: 1.75rem;
      height: 1.75rem;
      background-image: url("../img/select-icon.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
    }
    span {
      position: relative;
      z-index: 1;
      font-size: 1rem;
      line-height: 130%;
      font-weight: 500;
      color: rgb(28, 28, 30);
      padding: 0.75rem 2.1875rem;
    }
  }
  &__btn {
    max-width: 25rem;
    width: 100%;
  }
  &__icon {
    width: 13.75rem;
    img {
      width: 100%;
    }
  }
  &__img {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 61.5vw;
    img {
      width: 100%;
    }
  }
}
.section-group {
  position: relative;
  background: rgb(233, 233, 233);
  padding-bottom: 6.25rem;
  &__img {
    position: absolute;
    top: 2rem;
    left: 0;
    width: 12.5rem;
    img {
      width: 100%;
    }
  }
  &__img2 {
    position: absolute;
    top: 0;
    right: 0;
    width: 15.625rem;
    img {
      width: 100%;
    }
  }
  &__img3 {
    position: absolute;
    left: 0;
    bottom: 2.5rem;
    width: 24.0625rem;
    img {
      width: 100%;
    }
  }
  &__img4 {
    position: absolute;
    right: 0;
    bottom: 2.5rem;
    width: 24.0625rem;
    img {
      width: 100%;
    }
  }
}
.info {
  position: relative;
  z-index: 2;
  padding: 0;
  margin-top: -5rem;
  &__items {
    max-width: 77.5rem;
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3));
    border-radius: 2.1875rem;
    margin: 0 auto;
    overflow: hidden;
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
