.quiz {
  padding-bottom: 3.75rem;
  &-inner {
    max-width: 57.1875rem;
    background: #ffffff;
    box-shadow: 0 0.3125rem 3.125rem -0.625rem #cccccc;
    border-radius: 2.1875rem;
    padding: 2rem;
    margin: 0 auto;
  }
  &-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
  }
  &-body {
    display: none;
    &--active {
      display: block;
    }
  }
  &-img {
    width: 50%;
    img {
      width: 100%;
    }
  }
  &-desc {
    width: 50%;
  }
  &-title {
    font-size: 1.875rem;
    line-height: 130%;
    font-weight: 700;
    color: #333333;
  }
  &-btn {
    width: 5.625rem;
    margin-top: 1.875rem;
    span {
      font-size: 1rem;
      text-transform: unset;
      padding: 0.625rem 1rem;
    }
  }
  &-step {
    display: none;
    .quiz-title {
      font-size: 1.5rem;
      margin-bottom: 1.25rem;
    }
  }
  &-step--active {
    display: block;
  }
  &-step1 {
    .quiz-checkboxes {
      grid-template-columns: repeat(2, calc(50% - 0.75rem / 2));
    }
  }
  &-checkboxes {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.625rem;
  }
  &-checkbox {
    position: relative;
    display: flex;
    align-items: center;
    &:hover {
      box-shadow: 0 0.0625rem 0.3125rem 0 rgba(88, 85, 129, 0.15);
    }
    input {
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
      cursor: pointer;
    }
    input:checked ~ label {
      border-color: #dec29e;
      &:after {
        background-image: url("../img/check3.svg");
        background-color: #dec29e;
        border-color: #dec29e;
      }
    }
    label {
      position: relative;
      width: 100%;
      height: 100%;
      font-size: 1rem;
      line-height: 130%;
      color: #333333;
      border: 0.125rem solid #cccccc;
      border-radius: 0.375rem;
      padding: 0.75rem 0.75rem 0.75rem 2.75rem;
      &:after {
        content: '';
        position: absolute;
        top: 0.6875rem;
        left: 0.75rem;
        width: 1.25rem;
        height: 1.25rem;
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
        border: 0.0625rem solid rgba(0, 0, 0, .25);
        border-radius: 0.25rem;
      }
    }
  }
  &-nav {
    display: none;
    justify-content: space-between;
    align-items: center;
    margin-top: 1.25rem;
  }
  &-nav--active {
    display: flex;
  }
  &-prev {
    opacity: 0.5;
    pointer-events: none;
    span {
      font-size: 1rem;
      text-transform: unset;
      padding: 0.625rem 1rem;
    }
  }
  &-prev--active {
    opacity: 1;
    pointer-events: auto;
  }
  &-next {
    span {
      font-size: 1rem;
      text-transform: unset;
      padding: 0.625rem 1rem;
    }
    span:nth-child(2) {
      position: absolute;
      top: 100%;
      left: 50%;
      transform: translate(-50%, 0);
      padding: 0;
      font-size: 0.75rem;
      font-weight: 400;
      white-space: nowrap;
    }
  }
  .quiz-error {
    display: none;
    font-size: 1rem;
    line-height: 130%;
    font-weight: 500;
    color: red;
    margin-top: 0.625rem;
  }
  .quiz-error--active {
    display: block;
  }
  &-form {
    display: none;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
  }
  &-form--active {
    display: flex;
  }
  &-form__desc {
    width: 50%;
    .quiz-title {
      font-size: 1.5rem;
    }
  }
  &-form__form {
    width: 50%;
  }
  .form_block {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.75rem;
    input {
      height: 2.625rem;
      background: #f3f3f3;
      border-color: rgba(0, 0, 0, .25);
      color: #333333;
      &::placeholder {
        color: #333333 !important;
      }
    }
  }
  .form-btn {
    max-width: 7.8125rem;
    width: 100%;
    span {
      font-size: 1rem;
      text-transform: unset;
      padding: 0.625rem 1rem;
    }
  }
  .agreement-check {
    label {
      &:after {
        background-color: #597ed5;
        border-color: #597ed5;
      }
    }
  }
}
