    background-image: url("../img/staff-bg.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    pointer-events: none;
  }
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 50%;
    background: #ffffff;
    pointer-events: none;
  }
  h2 {
    max-width: 51.875rem;
    text-align: center;
    margin: 0 auto;
  }
  p {
    text-align: center;
    font-size: 1.5rem;
    line-height: 130%;
    color: rgb(75, 75, 75);
    margin: 1.5rem 0 0 0;
  }
  &__slider {
    position: relative;
    max-width: 81.25rem;
    margin: 0 auto;
  }
  .slick-list {
    padding-top: 20.625rem;
    margin: 0 -4.375rem;
  }
  .slick-track {
    display: flex;
    align-items: stretch;
  }
  .slick-slide {
    height: auto;
    margin: 0 4.375rem;
    & > div {
      height: 100%;
    }
  }
  &__prev {
    left: -11.25rem;
    bottom: 50%;
    transform: translate(0, 50%);
    width: 5.625rem;
    height: 5.625rem;
    background: linear-gradient(0.00deg, rgb(236, 212, 179) 9.502%,rgb(204, 170, 130) 100%);
    border-radius: 50%;
    svg {
      position: relative;
      z-index: 1;
      width: 0.75rem;
      height: auto;
      fill: transparent;
    }
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: calc(100% - 0.125rem);
      height: calc(100% - 0.125rem);
      background: #1C1C1E;
      border-radius: 50%;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
    &:hover {
      box-shadow: 0 0 0.25rem 0 rgb(204, 170, 130);
      &:after {
        opacity: 1;
      }
      svg path {
        stroke: #ffffff;
      }
    }
  }
  &__next {
    right: -11.25rem;
    bottom: 50%;
    transform: translate(0, 50%);
    width: 5.625rem;
    height: 5.625rem;
    background: linear-gradient(0.00deg, rgb(236, 212, 179) 9.502%,rgb(204, 170, 130) 100%);
    border-radius: 50%;
    svg {
      position: relative;
      z-index: 1;
      width: 0.75rem;
      height: auto;
      fill: transparent;
    }
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: calc(100% - 0.125rem);
      height: calc(100% - 0.125rem);
      background: #1C1C1E;
      border-radius: 50%;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
    &:hover {
      box-shadow: 0 0 0.25rem 0 rgb(204, 170, 130);
      &:after {
        opacity: 1;
      }
      svg path {
        stroke: #ffffff;
      }
    }
  }
}
