#header {
  position: absolute;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  color: #fff;
  padding: 0;
}
#header-top {
  position: relative;
  z-index: 3;
  border: none;
  padding: 0.75rem 0;
  .header-top__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 3.5rem;
  }
  .header-top__left {
    display: flex;
    align-items: center;
    gap: 1.5rem;
  }
  .header-top__right {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 3.5rem;
  }
  .header-top__logo {
    width: 8.125rem;
    display: block;
    img {
      width: 100%;
    }
  }
  .header-top__text {
    font-family: Cormorant;
    font-size: 1.5rem;
    line-height: 130%;
    font-weight: 700;
    span {
      color: rgb(186, 179, 145);
    }
  }
  .header-top__items {
    display: flex;
    gap: 3.5rem;
  }
  .header-top__item {
    max-width: 12.5rem;
  }
  .header-top__item-text {
    font-size: 0.875rem;
    line-height: 130%;
    font-weight: 500;
    span {
      display: block;
      text-transform: uppercase;
      color: rgb(186, 179, 145);
      margin-bottom: 0.5rem;
    }
  }
  .nice-select {
    width: auto;
    background: transparent !important;
    border: none;
    padding: 0;
    font-size: 0.875rem;
    line-height: 130%;
    font-weight: 500;
    color: #ffffff;
    text-decoration: underline;
  }
  .open {
    overflow: unset;
  }
  .list {
    top: 140%;
    min-width: 8.75rem;
    background: #fff;
    border-radius: 0.4375rem;
    padding: 0.75rem;
    overflow: unset;
    &:after {
      content: '';
      position: absolute;
      top: -0.875rem;
      left: 0.75rem;
      width: 1.75rem;
      height: 1.75rem;
      background-image: url("../img/select-icon.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
    }
    li {
      font-size: 0.875rem;
      line-height: 140%;
      font-weight: 500;
      color: rgb(51, 51, 51);
      border: none;
      padding: 0;
    }
    .selected,
    .focus {
      font-weight: 500;
      background: transparent;
      text-decoration: underline;
    }
    li:hover {
      background: transparent;
      text-decoration: underline;
    }
  }
  .header-top__group {
    display: flex;
    gap: 2.5rem;
  }
  .header-top__socials {
    display: flex;
    align-items: center;
    gap: 0.75rem;
  }
  .header-top__social {
    position: relative;
    width: 2.5rem;
    height: 2.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(0.00deg, rgb(236, 212, 179) 9.502%,rgb(204, 170, 130) 100%);
    border-radius: 0.4375rem;
    svg {
      position: relative;
      z-index: 1;
    }
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: calc(100% - 0.125rem);
      height: calc(100% - 0.125rem);
      background: #1C1C1E;
      border-radius: 0.4375rem;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
    &:hover {
      box-shadow: 0 0 0.25rem 0 rgb(204, 170, 130);
      &:after {
        opacity: 1;
      }
      svg path {
        fill: #ffffff;
      }
    }
  }
  .header-top__btns {
    display: flex;
    flex-direction: column;
  }
  .header-top__phone {
    font-size: 1.25rem;
    line-height: 130%;
    font-weight: 700;
    &:hover {
      color: transparent;
      background: linear-gradient(0.00deg, rgb(236, 212, 179),rgb(204, 170, 130));
      -webkit-background-clip: text;
      background-clip: text;
    }
  }
  .header-top__btn {
    justify-content: flex-end;
    background: transparent;
    margin-top: 0.5rem;
    span {
      font-size: 0.875rem;
      line-height: 130%;
      font-weight: 500;
      color: transparent;
      background: linear-gradient(0.00deg, rgb(236, 212, 179),rgb(204, 170, 130));
      -webkit-background-clip: text;
      background-clip: text;
      padding: 0;
    }
    &:hover {
      span {
        color: #ffffff;
      }
    }
  }
  .header-top__mob {
    display: none;
    gap: 1.5rem;
  }
  .search-btn {
    width: 2.25rem;
    height: 2.25rem;
    display: none;
    justify-content: center;
    align-items: center;
    background: linear-gradient(0.00deg, rgb(236, 212, 179) 9.502%,rgb(204, 170, 130) 100%);
    border-radius: 0.4375rem;
    svg {
      position: relative;
      z-index: 1;
    }
    svg path {
      fill: #1C1C1E;
    }
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: calc(100% - 0.125rem);
      height: calc(100% - 0.125rem);
      background: #1C1C1E;
      border-radius: 0.4375rem;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
    &:hover {
      box-shadow: 0 0 0.25rem 0 rgb(204, 170, 130);
      &:after {
        opacity: 1;
      }
      svg path {
        fill: #ffffff;
      }
    }
  }
  .menu {
    display: none;
  }
}
#header-bottom {
  .header-bottom__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
    border-top: 0.125rem solid rgb(75, 75, 75);
    border-bottom: 0.125rem solid rgb(75, 75, 75);
    padding: 0.5625rem 0;
  }
}
.menu {
  display: flex;
  align-items: center;
  gap: 3rem;
}
.menu-item {
  &:after,
  &:before {
    display: none;
  }
  a {
    font-size: 1rem !important;
    line-height: 130%;
    font-weight: 700 !important;
    text-transform: uppercase;
    background: linear-gradient(0.00deg, rgb(236, 212, 179),rgb(204, 170, 130));
    color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
    border: none !important;
    padding: 0 !important;
  }
  a:hover {
    color: #ffffff;
  }
}
.search {
  position: relative;
  input {
    width: 16.25rem;
    height: 2.9375rem;
    background: rgb(255, 255, 255);
    border-radius: 0.4375rem;
    padding: 0 1.25rem 0 3.375rem;
    font-size: 0.875rem;
    line-height: 130%;
    font-weight: 500;
    text-transform: uppercase;
    color: #1C1C1E;
    &::placeholder {
      color: rgb(175, 175, 176);
    }
    svg {
      width: 100%;
      height: auto;
    }
  }
  &-btn {
    position: absolute;
    z-index: 1;
    top: 50%;
    left: 1.25rem;
    transform: translate(0, -50%);
    width: 1.25rem;
    cursor: pointer;
  }
}
.burger {
  position: relative;
  z-index: 12;
  width: 2rem;
  height: 1.25rem;
  cursor: pointer;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 0.1875rem;
    background: #ffffff;
    border-radius: 0.1875rem;
    transition: all 0.5s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
  &--active {
    span:nth-child(1) {
      top: 0.5rem;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 0.5rem;
      transform: rotate(45deg);
    }
  }
}
.search-mob {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(28, 28, 30, 1);
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
  &--active {
    opacity: 1;
    pointer-events: auto;
  }
  .search-close {
    width: 2rem;
    height: 2rem;
    visibility: unset;
    svg {
      width: 100%;
      height: auto;
    }
    svg path {
      fill: #ffffff;
    }
  }
  .search input {
    width: 31.25rem;
    height: 3.5rem;
  }
}


@media screen and (max-width: 1600px) {
  #header-top {
    .header-top__left {
      gap: 1rem;
    }
    .header-top__text {
      font-size: 1.225rem;
    }
    .header-top__right {
      gap: 1.5rem;
    }
    .header-top__items {
      gap: 1.5rem;
    }
    .header-top__group {
      gap: 1.5rem;
    }
  }
}

@media screen and (max-width: 1500px) {
  #header-bottom {
    .header-bottom__inner {
      gap: 2rem;
    }
  }
  .menu {
    gap: 2rem;
  }
}

@media screen and (max-width: 1399px) {
  #header-top {
    .header-top__inner {
      gap: 2rem;
    }
    .header-top__logo {
      width: 6.25rem;
    }
    .header-top__phone {
      font-size: 1.125rem;
    }
  }
  #header-bottom {
    .header-bottom__inner {
      gap: 1.5rem;
    }
  }
  .menu {
    gap: 1.5rem;
  }
}

@media screen and (max-width: 1300px) {
  #header-top {
    .header-top__group {
      flex-shrink: 0;
      flex-direction: column;
      gap: 1rem;
    }
    .header-top__socials {
      justify-content: flex-end;
    }
  }
  .menu-item {
    a {
      font-size: 0.875rem !important;
    }
  }
  .search {
    input {
      width: 15rem;
    }
  }
}

@media screen and (max-width: 1200px) {
  #header-top {
    .header-top__right {
      flex-grow: unset;
    }
    .header-top__logo {
      width: 5rem;
    }
    .header-top__text {
      font-size: 1.125rem;
    }
    .header-top__item-text {
      font-size: 0.75rem;
    }
    .header-top__social {
      width: 2.25rem;
      height: 2.25rem;
      svg {
        max-width: 1.25rem;
        height: auto;
      }
    }
    .nice-select {
      font-size: 0.75rem;
    }
    .list {
      li {
        font-size: 0.75rem;
      }
    }
    .search-btn {
      position: relative;
      top: unset;
      left: unset;
      transform: unset;
      display: flex;
    }
  }
  #header-bottom {
    .header-bottom__inner {
      padding: 0.75rem 0;
    }
    .search {
      display: none;
    }
  }
  .menu {
    width: 100%;
    justify-content: space-between;
  }
  .search {
    input {
      display: none;
    }
  }
  .search-mob {
    input {
      display: block;
    }
  }
}

@media screen and (max-width: 1023px) {
  #header-top {
    .header-top__right {
      position: fixed;
      z-index: 10;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      flex-direction: column;
      justify-content: start;
      align-items: center;
      text-align: center;
      background: rgb(28, 28, 30);
      padding: 7.5rem 2rem 5rem;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      overflow-y: auto;
      &--active {
        opacity: 1;
        pointer-events: auto;
      }
      .city {
        display: none;
      }
    }
    .header-top__items {
      flex-direction: column;
      align-items: center;
    }
    .header-top__socials {
      justify-content: center;
      .search-btn {
        display: none;
      }
    }
    .header-top__btn {
      justify-content: center;
    }
    .header-top__mob {
      display: flex;
    }
    .menu {
      display: flex;
      flex-direction: column;
      text-align: center;
    }
  }
  #header-bottom {
    display: none;
  }
}

@media screen and (max-width: 768px) {
  #header-top {
    .header-top__mob {
      align-items: center;
      .header-top__btns {
        display: none;
      }
    }
  }
  .search-mob {
    .search {
      width: 100%;
      margin: 2rem;
    }
    .search input {
      width: 100%;
    }
  }
}

@media screen and (max-width: 576px) {
  #header-top {
    .header-top__text {
      display: none;
    }
    .header-top__mob {
      gap: 1rem;
    }
  }
}