html {
  font-size: max(calc(16px * var(--coef_h)), 15px);
}
body {
  font-family: Montserrat, sans-serif;
  background: rgb(236, 236, 236);
}
.fixed-body {
  overflow: hidden;
}
.wrapper {
  position: relative;
  overflow: hidden;
}
.container-fluid {
  max-width: 1740px;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: Cormorant;
  font-weight: 700;
}
.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  span {
    min-width: auto;
  }
}
img {
  object-fit: cover;
}