.btn-beige {
  background: linear-gradient(0.00deg, rgb(236, 212, 179) 9.502%,rgb(204, 170, 130) 100%);
  border-radius: 0.4375rem;
  span {
    font-family: Montserrat;
    font-size: 1.125rem;
    line-height: 130%;
    font-weight: 500;
    text-transform: uppercase;
    color: rgb(0, 0, 0);
    padding: 1.875rem 0.5rem;
  }
  &:hover {
    box-shadow: 0 0.25rem 2.75rem 0 rgb(224, 196, 160);
  }
}
h2 {
  font-size: 3rem;
  line-height: 100%;
}
.form-group {
  border: none;
  padding: 0;
  margin: 0;
  input {
    width: 100%;
    height: 2.5rem;
    background: rgb(75, 75, 75);
    border-color: transparent;
    border-radius: 0.3125rem;
    padding: 0 1.25rem;
    font-size: 1rem;
    line-height: 130%;
    color: #ffffff;
    &::placeholder {
      color: rgb(181, 181, 181) !important;
    }
  }
  textarea {
    width: 100%;
    height: 5.375rem;
    background: rgb(75, 75, 75);
    border-color: transparent;
    border-radius: 0.3125rem;
    padding: 0.625rem 1.25rem;
    font-size: 1rem;
    line-height: 130%;
    color: #ffffff;
    resize: none;
    &::placeholder {
      color: rgb(181, 181, 181) !important;
    }
  }
}
.agreement-check {
  input {
    z-index: 2;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
  }
  input:checked ~ label {
    &:after {
      background-image: url("../img/check2.svg");
    }
  }
  label {
    font-size: 0.875rem;
    padding-left: 1.875rem;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 1.125rem;
      height: 1.125rem;
      background-repeat: no-repeat;
      background-position: center;
      background-size: auto;
      border: 0.0625rem solid rgb(255, 255, 255);
      border-radius: 0.3125rem;
    }
  }
}
.button {
  display: flex;
  justify-content: center;
  align-items: center;
}
.left-anim {
  transform: translate(-60%, 0);
  opacity: 0;
  transition: all 0.6s;
}
.left-anim.active {
  transform: translate(0,0);
  opacity: 1;
}
.right-anim {
  transform: translate(60%, 0);
  opacity: 0;
  transition: all 0.6s;
}
.right-anim.active {
  transform: translate(0,0);
  opacity: 1;
}
.navigation {
  display: inline-grid;
  grid-template-columns: 100%;
  gap: 1.25rem;
  padding: 3.125rem 0;
  a {
    font-size: 1.5rem;
    line-height: 130%;
    font-weight: 500;
    &:hover {
      color: rgb(236, 212, 179);
    }
  }
}

@media screen and (max-width: 1399px) {
  h2 {
    font-size: 2.5rem;
  }
}

@media screen and (max-width: 1023px) {
  .btn-beige {
    span {
      font-size: 1rem;
      padding: 1.5rem 0.5rem;
    }
  }
  h2 {
    font-size: 2rem;
  }
}

@media screen and (max-width: 576px) {
  h2 {
    font-size: 1.75rem;
  }
}