.footer-top {
  background: #000000;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
  }
  &__left {
    max-width: 32.8125rem;
    width: 100%;
    border-right: 0.125rem solid rgba(91, 91, 91, 0.7);
    padding: 3.75rem 6.25rem 3.75rem 0;
  }
  &__right {
    flex-grow: 1;
    padding: 3.75rem 0 3.75rem 6.25rem;
    .footer-top__items {
      gap: 5.3125rem;
    }
    .footer-top__subtitle {
      font-size: 1rem;
    }
  }
  &__items {
    display: flex;
    justify-content: space-between;
    gap: 2rem;
  }
  &__item {
    max-width: 17.5rem;
  }
  &__subtitle {
    font-size: 1.125rem;
    line-height: 130%;
    font-weight: 700;
    color: rgb(255, 255, 255);
  }
  ul {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.625rem;
    margin-top: 1rem;
    li {
      font-size: 1.125rem;
      line-height: 130%;
      color: rgb(195, 195, 195);
    }
    li:hover {
      color: #ffffff;
    }
  }
  &__group {
    display: inline-grid;
    grid-template-columns: 100%;
    gap: 0.625rem;
    margin-top: 1.25rem;
  }
  &__location,
  &__phone,
  &__email {
    display: flex;
    &:hover {
      p {
        text-decoration: unset;
        color: rgb(186, 179, 145);
      }
    }
    i {
      width: 1.3125rem;
      flex-shrink: 0;
      margin-right: 1rem;
    }
    p {
      font-size: 1rem;
      line-height: 130%;
      color: rgb(255, 255, 255);
      margin: 0;
    }
  }
  &__email {
    p {
      text-decoration: underline;
      text-underline-offset: 0.1875rem;
      color: rgb(186, 179, 145);
    }
  }
}
.footer-center {
  background: #000000;
  border-top: 0.125rem solid rgba(91, 91, 91, 0.7);
  padding: 2rem 0 4.5rem;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    max-width: 32.8125rem;
    width: 100%;
    display: flex;
    align-items: center;
    gap: 1.5rem;
    padding-right: 6.25rem;
  }
  &__right {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 6.25rem;
  }
  &__logo {
    width: 8.125rem;
    display: block;
    img {
      width: 100%;
    }
  }
  &__text {
    font-family: Cormorant;
    font-size: 1.5rem;
    line-height: 130%;
    font-weight: 700;
    color: #ffffff;
    span {
      color: rgb(186, 179, 145);
    }
  }
  &__wrap {
    display: flex;
    gap: 6.25rem;
  }
  &__phone {
    display: block;
    font-size: 1.375rem;
    line-height: 130%;
    font-weight: 700;
    color: #ffffff !important;
  }
  &__email {
    display: block;
    font-size: 1rem;
    line-height: 130%;
    text-decoration: underline;
    text-underline-offset: 0.1875rem;
    color: rgb(255, 247, 234) !important;
    margin-top: 0.5rem;
  }
  &__schedule {
    font-size: 1rem;
    line-height: 130%;
    color: #ffffff;
    strong {
      display: block;
      font-weight: 700;
    }
    p {
      color: rgb(255, 247, 234);
      margin-top: 0.5rem;
    }
  }
  &__socials {
    display: flex;
    align-items: center;
    gap: 0.75rem;
    margin-left: 6.25rem;
  }
  &__social {
    position: relative;
    width: 2.5rem;
    height: 2.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(0.00deg, rgb(236, 212, 179) 9.502%,rgb(204, 170, 130) 100%);
    border-radius: 0.4375rem;
    svg {
      position: relative;
      z-index: 1;
    }
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: calc(100% - 0.125rem);
      height: calc(100% - 0.125rem);
      background: #1C1C1E;
      border-radius: 0.4375rem;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
    &:hover {
      box-shadow: 0 0 0.25rem 0 rgb(204, 170, 130);
      &:after {
        opacity: 1;
      }
      svg path {
        fill: #ffffff;
      }
    }
  }
}
.footer-mob {
  display: none;
}
.footer-bottom {
  background: #ffffff;
  padding: 2rem 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
  }
  &__left {
    flex-shrink: 0;
  }
  &__right {
    max-width: 66.5625rem;
    display: flex;
    align-items: center;
    gap: 6.25rem;
    font-size: 1rem;
    line-height: 130%;
    color: rgb(51, 51, 51);
  }
  &__b2b {
    width: 18.75rem;
    display: block;
    img {
      width: 100%;
    }
  }
  &__text {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    gap: 1rem;
    p {
      margin: 0;
    }
  }
  &__info {
    a {
      text-decoration: underline;
      text-underline-offset: 0.1875rem;
    }
  }
}

@media screen and (max-width: 1700px) {
  .footer-top {
    &__left {
      max-width: 28rem;
      padding: 3.125rem 3.125rem 3.125rem 0;
    }
    &__right {
      padding: 3.125rem 0 3.125rem 3.125rem;
      .footer-top__items {
        gap: 2rem;
      }
    }
  }
  .footer-center {
    &__left {
      max-width: 28rem;
      padding-right: 3.125rem;
    }
    &__right {
      padding-left: 3.125rem;
    }
  }
  .footer-bottom {
    &__right {
      gap: 2rem;
    }
  }
}

@media screen and (max-width: 1500px) {
  .footer-top {
    &__left {
      max-width: 25rem;
      padding-right: 2.5rem;
    }
    &__right {
      padding-left: 2.5rem;
    }
    &__subtitle {
      font-size: 1rem;
    }
    ul {
      li {
        font-size: 1rem;
      }
    }
  }
  .footer-center {
    &__left {
      max-width: 25rem;
      padding-right: 2.5rem;
    }
    &__right {
      padding-left: 2.5rem;
    }
    &__logo {
      width: 6rem;
    }
    &__wrap {
      gap: 2rem;
    }
    &__socials {
      margin-left: 2rem;
    }
  }
}

@media screen and (max-width: 1399px) {
  .footer-top {
    &__left {
      max-width: 20rem;
      padding-right: 1.5rem;
    }
    &__right {
      padding-left: 1.5rem;
      .footer-top__items {
        gap: 1.5rem;
      }
    }
    &__items {
      gap: 1.5rem;
    }
    ul {
      li {
        font-size: 0.875rem;
      }
    }
    &__location,
    &__phone,
    &__email {
      p {
        max-width: 13.125rem;
        font-size: 0.875rem;
      }
    }
  }
  .footer-center {
    padding: 2rem 0;
    &__left {
      max-width: 20rem;
      padding-right: 1.5rem;
    }
    &__right {
      padding-left: 1.5rem;
    }
    &__text {
      font-size: 1.125rem;
    }
    &__phone {
      font-size: 1.125rem;
    }
  }
  .footer-bottom {
    &__right {
      font-size: 0.875rem;
    }
    &__text {
      flex-direction: column;
      align-items: start;
      gap: 0.5rem;
    }
  }
}

@media screen and (max-width: 1249px) {
  .footer-top {
    &__left {
      max-width: 18rem;
    }
    &__right {
      .footer-top__subtitle {
        font-size: 0.875rem;
      }
    }
    &__subtitle {
      font-size: 0.875rem;
    }
    ul {
      li {
        font-size: 0.75rem;
      }
    }
    &__location,
    &__phone,
    &__email {
      i {
        width: 1rem;
        margin-right: 0.625rem;
      }
      p {
        font-size: 0.75rem;
      }
    }
  }
  .footer-center {
    &__left {
      max-width: 18rem;
    }
  }
}

@media screen and (max-width: 1023px) {
  .footer-top {
    padding: 3.125rem 0;
    &__inner {
      flex-direction: column;
      gap: 2rem;
    }
    &__left {
      max-width: 100%;
      display: flex;
      justify-content: space-between;
      gap: 2rem;
      border-right: unset;
      padding: 0;
    }
    &__right {
      padding: 0;
    }
  }
  .footer-center {
    display: none;
  }
  .footer-mob {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    &__left {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      gap: 1.5rem;
    }
    &__right {
      display: flex;
      flex-wrap: wrap;
      gap: 1.5rem;
    }
    .footer-center__wrap {
      flex-direction: column;
      gap: 1.5rem;
    }
    .footer-center__socials {
      align-items: start;
      margin: 0;
    }
  }
  .footer-bottom {
    padding: 1.5rem 0;
    &__inner {
      align-items: start;
    }
    &__right {
      flex-direction: column;
      align-items: start;
      gap: 1rem;
    }
    &__text {
      flex-direction: row;
      gap: 1rem;
    }
  }
}

@media screen and (max-width: 768px) {
  .footer-top {
    &__right {
      .footer-top__items {
        display: grid;
        grid-template-columns: repeat(2, calc(50% - 1.5rem / 2));
      }
    }
  }
  .footer-bottom {
    &__right {
      font-size: 0.75rem;
    }
    &__b2b {
      width: 15rem;
    }
    &__text {
      flex-direction: column;
      gap: 0.3125rem;
    }
  }
}

@media screen and (max-width: 576px) {
  .footer-bottom {
    &__inner {
      flex-direction: column;
    }
    &__text {
      flex-direction: row;
      gap: 1rem;
    }
  }
}

@media screen and (max-width: 499px) {
  .footer-top {
    &__left {
      flex-direction: column;
    }
    &__items {
      flex-direction: column;
      grid-template-columns: 100% !important;
    }
  }
}